import { Menu, Button, Tooltip, message } from "antd";
import { StarFilled, StarOutlined } from "@ant-design/icons";
import React from "react";
import { addFavoriteItem, deleteFavoriteItem } from "../utils";

function MenuItem({ items, loggedIn, favOnChange }) {
  return items?.map((item) => (
    <Menu.Item key={item.id}>
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          width: "100%",
          minWidth: 0, // Important for text truncation
        }}
      >
        <div
          style={{
            flex: 1,
            minWidth: 0,
            marginRight: "24px",
          }}
        >
          <Tooltip title={`${item.broadcaster_name} - ${item.title}`}>
            <a
              href={item.url}
              target="_blank"
              rel="noopener noreferrer"
              style={{
                display: "block",
                whiteSpace: "nowrap",
                overflow: "hidden",
                textOverflow: "ellipsis",
              }}
            >
              {`${item.broadcaster_name} - ${item.title}`}
            </a>
          </Tooltip>
        </div>

        {loggedIn && (
          <div
            style={{
              flexShrink: 0,
              position: "relative",
              zIndex: 1,
            }}
          >
            <FavoriteButton
              item={item}
              favOnChange={favOnChange}
              isFavorite={true}
            />
          </div>
        )}
      </div>
    </Menu.Item>
  ));
}

function FavoriteButton({ item, isFavorite, favOnChange }) {
  const handleFavoriteClick = (e) => {
    e.stopPropagation();
    if (isFavorite) {
      deleteFavoriteItem(item)
        .then(() => {
          favOnChange();
          message.success("Removed from favorites");
        })
        .catch((err) => {
          message.error(err.message);
        });
    } else {
      addFavoriteItem(item)
        .then(() => {
          favOnChange();
          message.success("Added to favorites");
        })
        .catch((err) => {
          message.error(err.message);
        });
    }
  };

  return (
    <Tooltip
      title={isFavorite ? "Remove from favorite list" : "Add to favorite list"}
    >
      <Button
        shape="circle"
        icon={isFavorite ? <StarFilled /> : <StarOutlined />}
        onClick={handleFavoriteClick}
      />
    </Tooltip>
  );
}

export default MenuItem;
